*{
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.wrapper {
  box-sizing: border-box;
  width: 80%;
  margin: auto;
  box-shadow: 5px 5px 5px 5px rgb(0, 0, 0);
}

header {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  position:-webkit-sticky;
  position:sticky;
  top: 0;
  z-index: 1;
}

nav {
  background-color: rgb(6, 119, 15);
  color: azure;
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 2px;
  position:-webkit-sticky;
  position:sticky;
  top: 18%;
  z-index: 1;
}

nav.lab{
  background-color: rgb(10, 105, 194);
  color: azure;
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 2px;
}

nav.lab ul:hover {
  background-color: rgb(64, 142, 216);
  border-radius: 2px;
}

nav li {
  display: flex;
  flex-direction: row;
  width: 100%;
  align-items: center;
}

nav ul {
  display: flex;
  box-sizing: content-box;
  list-style-type: none;
  align-items: center;
  margin: 10px;
  margin-left: 0px;
  height: 100%;
  padding-left: 10px;
  padding-right: 10px;
}

nav ul:hover {
  background-color: rgb(0, 94, 16);
  border-radius: 2px;
}

nav a{
  text-decoration: none;
  color: azure;
}

section {
  background-color: rgb(240, 240, 240);
  overflow: hidden;
  padding: 1px;
  display: block;
  justify-content: center;
  padding-bottom: 20px;
  height: auto;
}

.content {
  padding: 10px;
  color: red;
}

.addressdiv{
  width: 30%;
}

footer {
  font-size: smaller;
  background-color: rgb(211, 211, 211);
  display: flex;
  padding: 5px;
  justify-content: flex-end;
}

a{
  text-decoration: none;
  color: black;
}

.ptregwrapper {
  display: flex;
  padding: 10px;
  margin-bottom: 10px;
  flex-direction: column;
}

.tablewrapper {
  display: block;
  margin: auto;
  width: 80%;
}

label {
  font-size: 0.9rem;
}

.form-content{
  display: block;
  margin: 5px 30px -1px 1px;
}

hr{
  margin-bottom: 5px;
}

.form-group{
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 5px;
}


#form-field{
  text-align: center;
  width: 100%;
  height: 1.5rem;
  background-color: transparent;
  color: rgb(0, 0, 0);
  border: 1px solid black;
}

#form-field-address{
  text-align: center;
  width: 150%;
  height: 1.5rem;
  background-color: transparent;
  color: rgb(0, 0, 0);
  border: 1px solid black;
}
.form-content select{
  width:100%;
